<template>
  <div class="InstallImage">
    <HeaderNavigation />
    <div class="InstallImageBox">
      <h2>安裝後</h2>
      <div class="InstallImageBoxUploading wrap">
        <div class="newInputBox">
          <div class="t">原水水壓</div>
          <input type="text" v-model="WaterPressure" placeholder="請填寫原水水壓"  />
          <div class="d">kg/cm²</div>
        </div>
        <div class="newInputBox">
          <div class="t">原水TDS</div>
          <input type="text" v-model="TDS" placeholder="請填寫原水TDS" />
          <div class="d">PPM</div>
        </div>
        <div class="newInputBox">
          <div class="t">餘氯</div>
          <input type="text" v-model="Chlorine" placeholder="請填寫餘氯" />
        </div>
        <div class="newInputBox">
          <div class="t">原水PH值</div>
          <input type="text" v-model="PH" placeholder="請填寫原水PH值" />
        </div>
      </div>
      <h2>廚下空間照<small>Upload photos</small></h2>
      <div class="InstallImageBoxImage">
        <div class="listbox">
          <div class="list">
            <van-uploader
              v-model="fileList"
              @delete="deleteImg1"
              :after-read="beforeRead1"
              :max-count="1"
              :preview-full-image="false"
              @click-preview="openBigImg1"
            >
              <img src="@/assets/image/uploading.png" alt="" />
            </van-uploader>
            <div class="txt">{{imgListTitle}}</div>
          </div>
          <div class="list">
            <van-uploader
              v-model="fileList2"
              @delete="deleteImg2"
              :after-read="beforeRead2"
              :max-count="1"
              :preview-full-image="false"
              @click-preview="openBigImg2"
            >
              <img src="@/assets/image/uploading.png" alt="" />
            </van-uploader>
            <div class="txt">{{imgList2Title}}</div>
          </div>
          <div class="list">
            <van-uploader
              v-model="fileList3"
              @delete="deleteImg3"
              :after-read="beforeRead3"
              :max-count="1"
              :preview-full-image="false"
              @click-preview="openBigImg3"
            >
              <img src="@/assets/image/uploading.png" alt="" />
            </van-uploader>
            <div class="txt">{{imgList3Title}}</div>
          </div>
        </div>
      </div>
      <p class="vanFieldTitle" v-show="titleStatus">此欄不得為空</p>
      <h2>掃描機體QR Code<small>Scan QR Code</small></h2>
      <div class="InstallImageBoxUploading">
        <van-field v-model="QRcode" placeholder="請輸入製造號碼" />
        <van-uploader
          :after-read="beforeReadCode"
          class="InstallImageBoxUploadingFlie"
        >
          <span>上傳QR Code</span>
        </van-uploader>
      </div>
      <p class="vanFieldTitle" v-show="titleStatuss">此欄不得為空</p>
      <!-- <p class="vanFieldTitle" v-show="titleStatuA">濾心製號不合法</p> -->
      <h2>實收金額<small>Price</small></h2>
      <div class="InstallImageBoxUploadings">
        <van-field v-model="Price" placeholder="請輸入實收金額" />
      </div>
      <p class="vanFieldTitle" v-show="PriceStatuss">此欄不得為空</p>
      <div class="InstallImageBoxBottom" @click="nextStep()">下一步</div>
    </div>
    <!-- 删除弹窗 -->
    <van-popup v-model="deleteShow" class="deleteShowVanPopup">
      <img src="@/assets/image/deleteImg.png" alt="" />
      <h2>確定刪除</h2>
      <div>
        <span @click="cancel(deleteNum)">取消</span>
        <em @click="confirm()">確定</em>
      </div>
    </van-popup>
    <!-- 处理中弹窗 -->
    <van-popup v-model="inHandShow" class="vanPopupinHand">
      <img draggable="false" src="@/assets/image/inHand.png" alt="" />
      <h2>處理中，請稍候...</h2>
    </van-popup>
    <!-- 失败弹窗 -->
    <van-popup v-model="failedShow" class="vanPopupfailed">
      <img draggable="false" src="@/assets/image/failed.png" alt="" />
      <h2>辨識失敗</h2>
      <h3>請再次嘗試，或洽總公司處理</h3>
      <div @click="failedShow = false">我知道了</div>
    </van-popup>

    <van-popup v-model="beDefeatedShow" class="beDefeatedShowStyle">
      <div class="img-box">
        <img  :src="bigImg" alt="" />
      </div>

      <h2>{{tit}}</h2>
    </van-popup>
  </div>
</template>

<script>
import { POST_CreateWaterInstallImage, POST_GetMFNO } from "@/utils/api";
import HeaderNavigation from "@/components/HeaderNavigation";
export default {
  name: "InstallImage",
  components: {
    HeaderNavigation
  },
  data() {
    return {
      WaterPressure: "",
      TDS: "",
      Chlorine: "",
      PH: "",
      titleStatus: false,
      beDefeatedShow:false,
      titleStatuss: false,
      PriceStatuss: false,
      titleStatuA: false,
      deleteShow: false,
      inHandShow: false,
      failedShow: false,
      QRcode: "",
      Price: "",
      files: "",
      fileList: [],
      fileList2: [],
      fileList3: [],
      fileFormData: "",
      fileFormDataList: [],
      fileImg: "",
      bigImg:"",
      tit:"",
      deleteNum:"",
      imgListTitle:"進水閥開關處",
      imgList2Title:"機體燈號",
      imgList3Title:"廚下安裝後照片",
      imgList:"",
      file_switch: "",
      file_light: "",
      file_complete: ""
    };
  },
  watch: {
    value: function() {
      if (this.value) {
        this.titleStatuss = false;
      }
    }
  },
  methods: {
    openBigImg1(file){
      this.beDefeatedShow = true;
      this.bigImg = file.content;
      this.tit = this.imgListTitle;
    },
    openBigImg2(file){
      this.beDefeatedShow = true;
      this.bigImg = file.content;
      this.tit = this.imgList2Title;
    },
    openBigImg3(file){
      this.beDefeatedShow = true;
      this.bigImg = file.content;
      this.tit = this.imgList3Title;
    },
    // 图片1上传
    beforeRead1(file) {
      this.file_switch = file.file;
    },
    // 图片2上传
    beforeRead2(file) {
      this.file_light = file.file;
    },
    // 图片3上传
    beforeRead3(file) {
      this.file_complete = file.file;
    },
    beforeReadCode(file) {
      let UofIds = new FormData();
      UofIds.append("file", file.file);
      this.inHandShow = true;
      POST_GetMFNO(UofIds).then(
        response => {
          this.QRcode = response.Data;
          this.inHandShow = false;
        },
        // eslint-disable-next-line no-unused-vars
        err => {
          this.inHandShow = false;
          this.failedShow = true;
        }
      );
    },

    // 点击删除时执行函数
    deleteImg1(item) {
      this.deleteShow = true;
      this.files = item;
      this.deleteNum = 1;
    },
    // 点击删除时执行函数
    deleteImg2(item) {
      this.deleteShow = true;
      this.files = item;
      this.deleteNum = 2;
    },
    // 点击删除时执行函数
    deleteImg3(item) {
      this.deleteShow = true;
      this.files = item;
      this.deleteNum = 3;
    },
    // 取消删除
    cancel() {
      this.deleteShow = false;
      if(this.deleteNum  == 1){
        if (!this.fileList) this.fileList.push(this.files)
        this.file_switch = this.files.file;
      }else if(this.deleteNum  == 2){
        if (!this.fileList2) this.fileList2.push(this.files)
        this.file_light = this.files.file;
      }else if(this.deleteNum  == 3){
        if (!this.fileList3) this.fileList3.push(this.files)
        this.file_complete = this.files.file;
      }
    },
    // 确认删除
    confirm() {
      this.deleteShow = false;
    },
    // 下一步
    nextStep() {
      if (this.file_switch == "" || this.file_light == "" || this.file_complete == "") {
        this.titleStatus = true;
      } else {
        this.titleStatus = false;
      }
      if (this.Price == "") {
        this.PriceStatuss = true;
      } else {
        this.PriceStatuss = false;
      }
      if (this.QRcode == "") {
        this.titleStatuss = true;
        // this.titleStatuA = false;
        return false;
      } else {
        this.titleStatuss = false;
        // var numReg = /^1BE/;
        // if (!numReg.test(this.QRcode)) {
        //   this.titleStatuA = true;
        //   return false;
        // }
        // if (this.QRcode.length > 14 || this.QRcode.length < 13) {
        //   this.titleStatuA = true;
        //   return false;
        // }
      }
      if (this.file_switch && this.file_light && this.file_complete && this.QRcode && this.Price) {
        this.titleStatuss = false;
        this.titleStatus = false;
        this.PriceStatuss = false;
        this.fileFormData = new FormData();
        this.fileFormData.append("file_switch", this.file_switch);
        this.fileFormData.append("file_light", this.file_light);
        this.fileFormData.append("file_complete", this.file_complete);
        this.fileFormData.append("InstallId", this.$route.query.id);
        POST_CreateWaterInstallImage(this.fileFormData).then(response => {
          if (!response.State) {
            return false;
          }
          this.$router.push({
            name: "Signature",
            query: {
              id: this.$route.query.id,
              name: this.$route.query.name,
              Mfno: this.QRcode,
              Price: this.Price,
              type: "install",
              WaterPressure: this.WaterPressure,
              TDS: this.TDS,
              Chlorine: this.Chlorine,
              PH: this.PH
            }
          });
        });
        // if (that.QRcode !== "XINI91231Q") {
        //   that.failedShow = true;
        // } else {
        //   that.$router.push("/Signature");
        // }
      }
    }
  }
};
</script>

<style lang="scss">
  .InstallImage {
    .van-overlay{
      background: #000;
      z-index: 99999;
    }
  }
  </style>
