        .van-uploader__input-wrapper{
          font-size: 13px;
        }
      }
    }
    .InstallImageBoxUploading.wrap{
      flex-wrap: wrap;
      height: auto;
    }
    .InstallImageBoxUploadings {
      display: flex;
      margin-top: 12px;
      margin-bottom: 16px;
      padding: 14px;
      height: 50px;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      .van-cell {
        display: inline-block;
        width: 100%;
        height: 50px;
        background: rgba(249, 249, 250, 1);
        border-radius: 4px;
        font-size: 13px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
      }
    }
    .vanFieldTitle {
      margin-top: -12px;
      margin-bottom: 16px;
      padding-left: 14px;
      font-size: 12px;
      font-weight: 500;
      color: rgba(254, 96, 59, 1);
      line-height: 17px;
    }
    .InstallImageBoxBottom {
      margin: 44px 0;
      height: 50px;
      background: linear-gradient(
        180deg,
        rgba(28, 118, 254, 1) 0%,
        rgba(28, 118, 254, 1) 100%
      );
      box-shadow: 0px 10px 40px 0px rgba(28, 118, 254, 0.3);
