      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 50px;
      text-align: center;
    }
  }
  .deleteShowVanPopup {
    padding: 40px 25px;
    width: 230px;
    height: 180px;
    background: rgba(255, 255, 255, 1);
    border-radius: 15px;
    text-align: center;
    h2 {
      margin-top: 26px;
      margin-bottom: 30px;
      font-size: 15px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 20px;
    }
    div {
      display: flex;
      justify-content: space-between;
      span {
        display: inline-block;
        width: 106px;
        height: 40px;
        background: rgba(229, 239, 255, 1);
        border-radius: 6px;
